








































/* 初始化 */

* {
  box-sizing: border-box;
}

html,
body,
#app {
  height: 100%;
  background-color: #F4F6F1;
}

body,
ul,
li {
  padding: 0;
  margin: 0;
  border: 0;
}

.main-body {
  height: 100%;
  padding-top: 62px;
}

.left {
  float: left;
}

.right {
  float: right;
}

.hide {
  display: none;
}
/* 公共组件 */

#app > div:first-child {
  height: 100%;
}

.vux-header {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#app .vux-header {
  width: 100%;
  position: fixed;
  z-index: 10;
  height: 62px;
  background-color: #A68A70;

}
#app .vux-header::after {
  content: "";
  z-index: 5;
  display: block;
  height: 10px;
  background-image: url(./assets/bolang.jpeg);
  background-size:35px 7px;
  opacity: 1;
  background-repeat: repeat-x;
  background-position:bottom;

}
.vux-header .vux-header-left {
  margin-top: 6px;
  height: 40px;
  font-weight: 500;
}
.vux-header .vux-header-left .vux-header-back {
  display: block;
  width: 30px;
  height: 30px;
}

#app .vux-header {
  padding-top: 12px;
}

#app .vux-header h1 {
  font-size: 24px;
}

#app .vux-header .vux-header-left .vux-header-back:before {
  border-width: 3px 0 0 3px;
}



