










































































































































.weui_cells {
  background-color: #F4F6F1;
}

.inline-calendar {
  background-color: #F4F6F1
}

@import '~vux/dist/vux.css';
.checker-popup {
  background: rgba(255, 255, 255, 0.5);
}

.vux-checker-box {
  padding: 0 15px;
  height: 200px;
  overflow-y: scroll;
}

.customtype-item {
  width: 60px;
  height: 30px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #ececec;
  border-radius: 5px;
  overflow-x: auto;
  white-space: nowrap;
  margin: 1px 2px;
  padding: 5px 5px;
}

.customtype-item-selected {
  background-color: #A1B7CC;
  color: #FFF;
}

.main-body .vux-no-group-title {
  margin-top: 0;
}

@media (max-height: 490px) {
  /* iphone4 为480px */
  .vux-checker-box {
    height: 150px;
  }
  .customtype-item {
    font-size: 12px;
  }
}



